$social-size:32px;
$social-border:0px;


.social {
    list-style: none;
    text-align: center;
    padding: 0;
    &__item {
        display: inline-block;
        margin: 5px;
    }
    &__link {
        color: $body-color;
        text-decoration: none;
        font-size: 1.5em;
        width: $social-size;
        height: $social-size;
        // line-height: calc(#{$social-size} - 2 * #{$social-border});
        // border: 2px solid $light-gray;
        border-radius: 50%;
        display: inline-block;
        @include tr; 
        @include hover{
            color: $primary-color;
        }
        
    }

}